import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { ChangeSem } from '../admin/admin-academics/semester-management/semester-management.component';
import { Event } from '../entity/event';
import { Holiday } from '../entity/holiday';
import { Notice } from '../entity/notice';
import { StudentProfile } from '../entity/student-profile';

@Injectable({
  providedIn: 'root'
})
export class AdminService {

  constructor(private http:HttpClient) { }

  getTotalBranches(){
    return this.http.get(`${environment.serverUrl}/admin/dashboard/get_branches`);
  }
  getTotalStudents(){
    return this.http.get(`${environment.serverUrl}/admin/dashboard/get_users/student`);
  }
  getTotalStaffMembers(){
    return this.http.get(`${environment.serverUrl}/admin/dashboard/get_users/staff`);
  }
  getTotalAdmins(){
    return this.http.get(`${environment.serverUrl}/admin/dashboard/get_users/admin`);
  }
  getHolidays(){
    return this.http.get(`${environment.serverUrl}/admin/dashboard/get_holidays`).pipe(
      map((data)=>{
        if(data instanceof Array){
          data.forEach((holiday)=>{
            holiday.holidayDate=new Date(holiday.holidayDate.toString());
          });
          return data;
        }
      })
    );
  }
  getBranchList(){
    return this.http.get(`${environment.serverUrl}/admin/get_branch_list`);
  }
  getRoleList(){
    return this.http.get(`${environment.serverUrl}/admin/get_role_list`).pipe(
      map(data=>{
        if(data instanceof Array){
          data.pop();
        }
        return data;
      })
    );
  }
  uploadNewNotice(not:Notice){
    const formData: FormData = new FormData();
    formData.append('description',not.description);
    formData.append('uploadBy',not.uploadBy);
    formData.append('roles',JSON.stringify(not.roles));
    formData.append('branches',JSON.stringify(not.branches));
    if(not.document!=null){
      formData.append('document',not.document);
    }

    return this.http.post(`${environment.serverUrl}/admin/notice/upload`,formData);
  }
  getAllNotice(){
    return this.http.get(`${environment.serverUrl}/admin/notice/get_all_notice`);
  }
  downloadFile(id:number){
    return this.http.get(`${environment.serverUrl}/admin/document/${id}`,{responseType:'arraybuffer'});
  }
  getAdminNoticeList(){
    return this.http.get(`${environment.serverUrl}/admin/notice/admin_notice_list/${sessionStorage.getItem("username")}`);
  }
  deleteNotice(id:number){
    return this.http.get(`${environment.serverUrl}/admin/notice/delete/${id}`);
  }
  getTop5Notice(){
    return this.http.get(`${environment.serverUrl}/admin/dashboard/get_top_5_notice`);
  }
  getTop5Event(){
    return this.http.get(`${environment.serverUrl}/admin/dashboard/get_top_5_event`);
  }
  getAllEvent(){
    return this.http.get(`${environment.serverUrl}/admin/event/get_all_event`);
  }
  getAdminEventList(){
    return this.http.get(`${environment.serverUrl}/admin/event/admin_event_list/${sessionStorage.getItem("username")}`);
  }
  deleteEvent(id:number){
    return this.http.get(`${environment.serverUrl}/admin/event/delete/${id}`);
  }
  uploadNewEvent(event:Event){
    const formData: FormData = new FormData();
    formData.append('description',event.description);
    formData.append('uploadBy',event.uploadBy);
    formData.append('branches',JSON.stringify(event.branches));
    if(event.document!=null){
      formData.append('document',event.document);
    }

    return this.http.post(`${environment.serverUrl}/admin/event/upload`,formData);
  }
  addHoliday(holiday:Holiday){
    return this.http.post(`${environment.serverUrl}/admin/holiday/upload`,holiday);
  }
  deleteHoliday(id:number){
    return this.http.get(`${environment.serverUrl}/admin/holiday/delete/${id}`);
  }
  getStudentProfileList(){
    return this.http.get(`${environment.serverUrl}/admin/student_profile/get_profile_list`);
  }
  checkStudentIdAvailability(id:number){
    return this.http.get(`${environment.serverUrl}/admin/student_profile/check-studentid-availability/${id}`);
  }
  addStudent(formdata:FormData){
    return this.http.post(`${environment.serverUrl}/admin/student_profile/add_student`,formdata);
  }
  deleteStudentById(id:string){
    return this.http.get(`${environment.serverUrl}/admin/student_profile/delete_student/${id}`);
  }
  changeStudentPassword(formdata:FormData){
    return this.http.post(`${environment.serverUrl}/admin/student_profile/change_student_password`,formdata);
  }
  updateStudent(sp:FormData){
    return this.http.post(`${environment.serverUrl}/admin/student_profile/update_student`, sp);
  }
  getAllAdminUsers(){
    return this.http.get(`${environment.serverUrl}/admin/admin_profile/get_all_admin_profiles`);
  }
  addAdminUser(formdata:FormData){
    return this.http.post(`${environment.serverUrl}/admin/admin_profile/add_admin`, formdata);
  }
  deleteAdminUser(id:string){
    return this.http.get(`${environment.serverUrl}/admin/admin_profile/delete_admin/${id}`);
  }
  changeAdminPassword(formdata:FormData){
    return this.http.post(`${environment.serverUrl}/admin/admin_profile/change_admin_password`, formdata);
  }
  checkIdAvailability(id:string){
    return this.http.get(`${environment.serverUrl}/admin/check-id-availability/${id}`);
  }
  getEnabledUsers(){
    return this.http.get(`${environment.serverUrl}/admin/getEnabledUsers`);
  }
  getDisabledUsers(){
    return this.http.get(`${environment.serverUrl}/admin/getDisabledUsers`);
  }
  enableUser(id:string){
    return this.http.get(`${environment.serverUrl}/admin/enableUser/${id}`);
  }
  disableUser(id:string){
    return this.http.get(`${environment.serverUrl}/admin/disableUser/${id}`);
  }
  getAllStaffList(){
    return this.http.get(`${environment.serverUrl}/admin/staff_profile/get_all_staff_list`);
  }
  addStaffMember(data:FormData){
    return this.http.post(`${environment.serverUrl}/admin/staff_profile/add_staff_member`, data);
  }
  deleteStaffMember(id:string){
    return this.http.get(`${environment.serverUrl}/admin/staff_profile/delete_staff_member/${id}`);
  }
  updateStaffMemeber(data:FormData){
    return this.http.post(`${environment.serverUrl}/admin/staff_profile/update_staff_member`,data);
  }
  checkAvailability(id:number){
    return this.http.get(`${environment.serverUrl}/admin/academics/branch/check_availability/${id}`);
  }
  addBranch(formdata:FormData){
    return this.http.post(`${environment.serverUrl}/admin/academics/branch/add_branch`,formdata);
  }
  deleteBranch(id:number){
    return this.http.get(`${environment.serverUrl}/admin/academics/branch/delete_branch/${id}`);
  }
  getExamList(){
    return this.http.get(`${environment.serverUrl}/admin/academics/exam/get_all_exam`);
  }
  addExamType(form:FormData){
    return this.http.post(`${environment.serverUrl}/admin/academics/exam/add_exam`,form);
  }
  deleteExamType(id:number){
    return this.http.get(`${environment.serverUrl}/admin/academics/exam/delete_exam/${id}`);
  }
  getAllSubject(){
    return this.http.get(`${environment.serverUrl}/admin/academics/subject/get_all_subject`)
  }
  addSubject(form:FormData){
    return this.http.post(`${environment.serverUrl}/admin/academics/subject/add_subject`,form);
  }
  deleteSubject(id:number){
    return this.http.get(`${environment.serverUrl}/admin/academics/subject/delete_subject/${id}`);
  }
  incrementSemester(set:Array<ChangeSem>){
    return this.http.post(`${environment.serverUrl}/admin/academics/semester/increment`,set);
  }
  decrementSemester(set:Array<ChangeSem>){
    return this.http.post(`${environment.serverUrl}/admin/academics/semester/decrement`,set);
  }

}
