import { Component, OnInit } from '@angular/core';
import { ErpUser } from 'src/app/entity/erp-user';
import { AdminService } from 'src/app/service/admin.service';

@Component({
  selector: 'app-admin-user-status-management',
  templateUrl: './admin-user-status-management.component.html',
  styleUrls: ['./admin-user-status-management.component.css']
})
export class AdminUserStatusManagementComponent implements OnInit {

  enableUserList:ErpUser[];
  disableUserList:ErpUser[];
  constructor(private adminService:AdminService) { }

  ngOnInit(): void {
    this.adminService.getEnabledUsers().subscribe(
      (resp:ErpUser[])=>{this.enableUserList=resp}
    );
    this.adminService.getDisabledUsers().subscribe(
      (resp:ErpUser[])=>{this.disableUserList=resp}
    );
  }

  enableUser(id:string){
    if(confirm('Do you want to enable "'+id+'" ?')){
      this.adminService.enableUser(id).subscribe(
        (response)=>{this.ngOnInit();}
      );
      //location.reload();
    }
  }
  disableUser(id:string){
    if(confirm('Do you want to disable "'+id+'" ?')){
      this.adminService.disableUser(id).subscribe(
        (response)=>{this.ngOnInit();}
      );
      //location.reload();
    }
  }
}
