import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class StudentService {

  constructor(private http:HttpClient) { }

  getProfile(){
    return this.http.get(`${environment.serverUrl}/student/profile/${sessionStorage.getItem("username")}`);
  }

  changePassword(password:string){
    return this.http.post<string>(`${environment.serverUrl}/student/change_password/${sessionStorage.getItem("username")}`,password);
  }
}
