import { HttpResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { StaffProfile } from '../entity/staff-profile';
import { LoginService } from '../service/login.service';
import { StaffService } from '../service/staff.service';

@Component({
  selector: 'app-staff',
  templateUrl: './staff.component.html',
  styleUrls: ['./staff.component.css']
})
export class StaffComponent implements OnInit {

  constructor(private loginService:LoginService, private router:Router, public sanitizer:DomSanitizer, private staffService:StaffService) { }

  public username?=sessionStorage.getItem('username');
  propic:any;
  ngOnInit(): void {
    this.staffService.getProfile(sessionStorage.getItem('username')).subscribe(
      (response:StaffProfile)=>{
          let imageurl = 'data:image/jpeg;base64, '+response.photo;
          this.propic=this.sanitizer.bypassSecurityTrustUrl(imageurl);
      }
    );
  }
  
  logout(){
    this.loginService.logout().subscribe((response:HttpResponse<any>)=>{this.router.navigate(['/login'])});
    sessionStorage.removeItem('username');
    sessionStorage.removeItem('basicauth');
    alert("You have been Successfully logout");
   }

}
