import { HttpErrorResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { element } from 'protractor';
import { pipe } from 'rxjs';
import { delay } from 'rxjs/operators';
import { StudentProfile } from 'src/app/entity/student-profile';
import { StudentService } from 'src/app/service/student.service';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {

  profile:StudentProfile ={id:"",
    studentName:'',
    studentSemester:'',
    branch:{branchId:null,branchName:''},
    studentDOB:null,
    studentMobileNumber:null,
    studentParentMobileNumber:null,
    studentEmail:'',
    studentAddress:'',
    studentPhoto:'',
    studentFees:null};
  password={npswd:'',cpswd:''};
  propic:any;
  match:boolean;
  success:boolean;
  constructor(private studentService:StudentService, private sanitizer:DomSanitizer, private router:Router) { }

  ngOnInit(): void {
    this.match=false;
    this.success=false;
    this.studentService.getProfile().subscribe(
      (response:StudentProfile)=>{
        this.profile=response;
        let imageurl = 'data:image/jpeg;base64, '+response.studentPhoto;
        this.propic=this.sanitizer.bypassSecurityTrustUrl(imageurl);
      },(error:HttpErrorResponse)=>{
        this.match=true;
      }
    );
    if(this.profile){
    }
  }

  clear(){
    this.match=false;
    this.success=false;
    this.password.cpswd=this.password.npswd="";
  }

  changePassword(){
    this.match=false;
    if(this.password.npswd!==this.password.cpswd){
      this.match=true;
      this.password.npswd=this.password.cpswd='';
    }else if(this.password.cpswd==='' && this.password.npswd===''){
      alert("Don't be smart!  Both passwords are empty..");
    }else{
      this.studentService.changePassword(this.password.npswd).subscribe(
        async (response)=>{
          this.success=true;
          sessionStorage.clear();
          alert("Password changed successfully...\nYou will be logged out...");
          document.getElementById("toggleButton").click();
          this.success=false;
          this.router.navigate(['/login']);
        },
        (error:HttpErrorResponse)=>{
          alert("Something gone wrong !");
        }
      );
    }
  }

}

