import { HttpErrorResponse, HttpHeaderResponse, HttpResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { User } from '../entity/user';
import { LoginService } from '../service/login.service';
import { StudentService } from '../service/student.service';

@Component({
  selector: 'app-student',
  templateUrl: './student.component.html',
  styleUrls: ['./student.component.css']
})
export class StudentComponent implements OnInit {

  public username?=sessionStorage.getItem('username');
  propic:any;
  constructor(private loginService:LoginService, private studentService:StudentService, private router:Router, private sanitizer:DomSanitizer) { }

  ngOnInit(): void {
    this.studentService.getProfile().subscribe(
      (response:any)=>{
        let imageurl = 'data:image/jpeg;base64, '+response.studentPhoto;
        this.propic=this.sanitizer.bypassSecurityTrustUrl(imageurl);
      },(error:HttpErrorResponse)=>{});
  }
  logout(){
   this.loginService.logout().subscribe((response:HttpResponse<any>)=>{this.router.navigate(['/login'])});
   sessionStorage.removeItem('username');
   sessionStorage.removeItem('basicauth');
   alert("You have been Successfully logout");
  }
  
}
