import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpEvent, HttpHeaders, HttpParams, HttpResponse } from '@angular/common/http'
import { catchError, map } from 'rxjs/operators';
import { LoginUser } from '../entity/loginuser';
import { environment } from 'src/environments/environment';
import { throwError } from 'rxjs';
import { Router } from '@angular/router';


@Injectable({
  providedIn: 'root'
})
export class LoginService {
  private user:LoginUser = {id:'',password:''};


  constructor(private http:HttpClient, private router:Router) { }

  login(usr:LoginUser){
    this.user.id=usr.id;
    this.user.password=usr.password;
    const headers= new HttpHeaders({
      'Content-Type':'application/json',
      'Authorization':'Basic '+btoa(usr.id+":"+usr.password)
    });
    const params = new HttpParams();
    params.append('username',`${usr.id}`);
    params.append('password',`${usr.password}`);

    return this.http.get(`${environment.serverUrl}/login`,{headers:headers,params:params}).pipe(
      map(
        data=>{
          sessionStorage.setItem('username',usr.id);
          let authString = 'Basic '+btoa(usr.id+':'+usr.password);
          sessionStorage.setItem('basicauth',authString);
          return data;
        }
      )
    );
  }

  async checkAuth(){
    return await this.http.get(environment.serverUrl+'/checkauthentication').toPromise();
  }

  logout(){
    return this.http.get(environment.serverUrl+'/logout').pipe(
      map(
        data=>{
          sessionStorage.removeItem('username');
          sessionStorage.removeItem('basicauth');
          sessionStorage.clear();
          return data;
        }
      )
    );
  }

}
