import { Component, OnInit } from '@angular/core';
import { Document } from 'src/app/entity/document';
import { Event } from 'src/app/entity/event';
import { Holiday } from 'src/app/entity/holiday';
import { Notice } from 'src/app/entity/notice';
import { StaffProfile } from 'src/app/entity/staff-profile';
import { StaffService } from 'src/app/service/staff.service';

@Component({
  selector: 'app-staff-dashboard',
  templateUrl: './staff-dashboard.component.html',
  styleUrls: ['./staff-dashboard.component.css']
})
export class StaffDashboardComponent implements OnInit {

  noticeList:Notice[];
  eventList:Event[];
  holidays:Holiday[];
  constructor(private staffService:StaffService) { }

  ngOnInit(): void {
    this.staffService.getProfile(sessionStorage.getItem('username')).subscribe(
      (profile:StaffProfile)=>{
        this.staffService.getNoticeList(profile.branch.branchId).subscribe(
          (resp:Notice[])=>{this.noticeList=resp;}
        );
        this.staffService.getEventList(profile.branch.branchId).subscribe(
          (resp:Event[])=>{this.eventList=resp;}
        );
      }
    );
    this.staffService.getHolidays().subscribe(
      (response:Holiday[])=>{this.holidays=response;}
    );
  }

  downloadFile(doc:Document){
    var a = document.createElement("a");
    document.body.appendChild(a);
    a.style.display = "none";
    this.staffService.downloadFile(doc.documentId).subscribe(
      (response)=>{
        doc.content=response;
        var blob = new Blob([response], {type: doc.type});
        var url = window.URL.createObjectURL(blob);
        a.href = url;
        a.download=doc.name;
        a.click()
        window.URL.revokeObjectURL(url);
      }
    );
    
  }
}
