import { Component, OnInit } from '@angular/core';
import { Event } from 'src/app/entity/event';
import { Holiday } from 'src/app/entity/holiday';
import { Notice } from 'src/app/entity/notice';
import { AdminService } from 'src/app/service/admin.service';

@Component({
  selector: 'app-admin-dashboard',
  templateUrl: './admin-dashboard.component.html',
  styleUrls: ['./admin-dashboard.component.css']
})
export class AdminDashboardComponent implements OnInit {

  constructor(private adminService:AdminService) { }
  totalStudents:number = 0;
  totalStaffMembers:number = 0;
  totalAdmins:number = 0;
  totalBranches:number = 0;
  holidays:Array<Holiday>;
  topNotices:Array<Notice>;
  topEvents:Array<Event>;

  ngOnInit(): void {
    this.getCounts();
    this.adminService.getHolidays().subscribe(
      (resp:Array<Holiday>)=>{
        this.holidays=resp;
      }
    );
    this.adminService.getTop5Notice().subscribe(
      (response:any)=>{
        this.topNotices=response;
      }
    );
    this.adminService.getTop5Event().subscribe(
      (response:any)=>{
        this.topEvents=response;
      }
    );
  }
  getCounts(){
    this.adminService.getTotalBranches().subscribe(
      (resp:number)=>{
        this.totalBranches=resp;
      }
    );
    this.adminService.getTotalStudents().subscribe(
      (resp:number)=>{
        this.totalStudents=resp;
      }
    );
    this.adminService.getTotalStaffMembers().subscribe(
      (resp:number)=>{
        this.totalStaffMembers=resp;
      }
    );
    this.adminService.getTotalAdmins().subscribe(
      (resp:number)=>{
        this.totalAdmins=resp;
      }
    );
  }

}
