import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AdminAcademicsComponent } from './admin-academics/admin-academics.component';
import { BranchManagementComponent } from './admin-academics/branch-management/branch-management.component';
import { ExamManagementComponent } from './admin-academics/exam-management/exam-management.component';
import { SemesterManagementComponent } from './admin-academics/semester-management/semester-management.component';
import { SubjectManagementComponent } from './admin-academics/subject-management/subject-management.component';
import { AdminDashboardComponent } from './admin-dashboard/admin-dashboard.component';
import { AdminNoticeEventComponent } from './admin-notice-event/admin-notice-event.component';
import { EventManagementComponent } from './admin-notice-event/event-management/event-management.component';
import { HolidayManagementComponent } from './admin-notice-event/holiday-management/holiday-management.component';
import { NoticeManagementComponent } from './admin-notice-event/notice-management/notice-management.component';
import { AdminProfileManagementComponent } from './admin-profiles/admin-profile-management/admin-profile-management.component';
import { AdminProfilesComponent } from './admin-profiles/admin-profiles.component';
import { AdminUserStatusManagementComponent } from './admin-profiles/admin-user-status-management/admin-user-status-management.component';
import { StaffProfileManagementComponent } from './admin-profiles/staff-profile-management/staff-profile-management.component';
import { StudentProfileManagementComponent } from './admin-profiles/student-profile-management/student-profile-management.component';
import { AdminComponent } from './admin.component';

const routes: Routes = [
  {
    path:'',
    component: AdminComponent,
    children:[
      {
        path:'dashboard',
        component:AdminDashboardComponent
      },
      {
        path:'notice-event',
        component:AdminNoticeEventComponent,
        children:[
          {path:'notices', component:NoticeManagementComponent},
          {path:'events', component:EventManagementComponent},
          {path:'holidays', component:HolidayManagementComponent},
          {path:'**', redirectTo:'/admin/notice-event/notices', pathMatch:'full'},
          {path:'', redirectTo:'/admin/notice-event/notices', pathMatch:'full'}
        ]
      },
      {
        path:'academics',
        component:AdminAcademicsComponent,
        children:[
          {path:'branch',component:BranchManagementComponent},
          {path:'exam', component:ExamManagementComponent},
          {path:'semester',component:SemesterManagementComponent},
          {path:'subject',component:SubjectManagementComponent},          
          {path:'**', redirectTo:'/admin/academics/branch', pathMatch:'full'},
          {path:'', redirectTo:'/admin/academics/branch', pathMatch:'full'}
        ]
      },
      {
        path:'profiles',
        component:AdminProfilesComponent,
        children:[
          {path:'student', component:StudentProfileManagementComponent},
          {path:'staff', component:StaffProfileManagementComponent},
          {path:'admin', component:AdminProfileManagementComponent},
          {path:'change-status', component:AdminUserStatusManagementComponent},
          {path:'**', redirectTo:'/admin/profiles/student', pathMatch:'full'},
          {path:'', redirectTo:'/admin/profiles/student', pathMatch:'full'}
        ]
      },
      {
        path:'',
        redirectTo: '/admin/dashboard',
        pathMatch:'full'
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AdminRoutingModule { }
