import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { ErpUser } from 'src/app/entity/erp-user';
import { AdminService } from 'src/app/service/admin.service';

@Component({
  selector: 'app-admin-profile-management',
  templateUrl: './admin-profile-management.component.html',
  styleUrls: ['./admin-profile-management.component.css']
})
export class AdminProfileManagementComponent implements OnInit {

  listAdmins:ErpUser[];
  passwordId:string ='';
  constructor(private adminService:AdminService) { }

  ngOnInit(): void {
    this.adminService.getAllAdminUsers().subscribe(
      (response:ErpUser[])=>{this.listAdmins = response},
      (error)=>{}
    );
  }

  addAdmin(form:NgForm){
    if(form.value.id == '' && form.value.password==''){
      alert("id or password required!");
    }
    else{
      this.adminService.checkIdAvailability(form.value.id).subscribe(
        (resp:boolean)=>{
          if(!resp){
            alert('given id already taken !');
          }
          else{
            const formdata:FormData = new FormData();
            formdata.append('id',form.value.id);
            formdata.append('password', form.value.password);
            this.adminService.addAdminUser(formdata).subscribe(
              (response)=>{alert("Admin added !")},
              (error)=>{alert('failed')}
            );
            document.getElementById('close-add-admin-form').click();
            location.reload();
          }
        }
      );
    }
  }

  changeAdminPassword(passwordChange:NgForm){
    if(passwordChange.value.password == ''){
      alert('Password is required !')
    }
    else{
      const formdata:FormData = new FormData();
      formdata.append('id', passwordChange.value.id);
      formdata.append('password', passwordChange.value.password);
      this.adminService.changeAdminPassword(formdata).subscribe(
        (response)=>{alert("password changed !")},
        (error)=>{alert('failed to change !')}
      );
      document.getElementById('close-add-admin-form').click();
      location.reload();
    }
  }

  openModal(id:string,mode:string){
    const container = document.getElementById("admin-profile");
    const button = document.createElement('button');
    button.type = 'button';
    button.style.display='none';
    button.setAttribute('data-toggle','modal');
    if(mode == 'add'){
      button.setAttribute('data-target', '#admin-register-modal');
    }
    if(mode == 'change-password'){
      this.passwordId=id;
      button.setAttribute('data-target','#change-admin-password');
    }
    container.appendChild(button);
    button.click();
  }
  deleteAdmin(id:string){
    if(id == sessionStorage.getItem("username")){
      alert("You can't delete your account!");
    }else{
      this.adminService.deleteAdminUser(id).subscribe(
        (response)=>{alert('admin '+id+' deleted!')},
        (error)=>{alert('error in deletation !')}
      );
      location.reload();
    }    
  }

}
