import { HttpErrorResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { ExamType } from 'src/app/entity/exam-type';
import { AdminService } from 'src/app/service/admin.service';

@Component({
  selector: 'app-exam-management',
  templateUrl: './exam-management.component.html',
  styleUrls: ['./exam-management.component.css']
})
export class ExamManagementComponent implements OnInit {
  examTypes:ExamType[];
  constructor(private adminService:AdminService) { }

  ngOnInit(): void {
    this.adminService.getExamList().subscribe(
      (response:ExamType[])=>{
        this.examTypes = response;
      },
      (error:HttpErrorResponse)=>{
        console.log(error.message);
      }
    );
  }

  addExamType(form:NgForm){
    const formdata:FormData = new FormData();
    formdata.append("examName",form.value.examName);
    formdata.append("marks", form.value.marks);
    this.adminService.addExamType(formdata).subscribe(
      response=>{
        alert('Exam added !');
        document.getElementById('close-exam-add').click();
        this.ngOnInit();
      },
      error=>{
        alert('failed to add !');
      }
    );
  }

  deleteExamType(id:number){
    if(confirm('do you want to delete exam?')){
      this.adminService.deleteExamType(id).subscribe(
        response=>{
          alert("deleted !");
          this.ngOnInit();
        },
        error=>{
          alert('failed !');
        }
      );
    }
  }

}
