import { Component, OnInit } from '@angular/core';
import { Holiday } from 'src/app/entity/holiday';
import { AdminService } from 'src/app/service/admin.service';

@Component({
  selector: 'app-holiday-management',
  templateUrl: './holiday-management.component.html',
  styleUrls: ['./holiday-management.component.css']
})
export class HolidayManagementComponent implements OnInit {

  holidays:Holiday[];
  holiday={holidayDate:'', holidayName:''};
  constructor(private adminService:AdminService) { }

  ngOnInit(): void {
    this.adminService.getHolidays().subscribe(
      (resp)=>{this.holidays=resp}
    );
  }
  addHoliday(){
    const h:Holiday={holidayId:null, holidayDate:new Date(this.holiday.holidayDate),holidayName:this.holiday.holidayName};
    this.adminService.addHoliday(h).subscribe(
      (response)=>{
        alert("Added Successfully!")
      }
    );
    const ele = document.getElementById("closeNewHoliday");
    ele.click();
    location.reload();
  }
  deleteHoliday(id:number){
    if(confirm("Are you sure to delete holiday ? ")){
      this.adminService.deleteHoliday(id).subscribe(
        (response)=>{alert("Holiday deleted successfully!");}
      );
      location.reload();
    }
  }

}
