import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class StaffService {

  constructor(private http:HttpClient) { }

  getProfile(id:string){
    return this.http.get(`${environment.serverUrl}/staff/get_profile/${id}`);
  }
  getNoticeList(id:number){
    return this.http.get(`${environment.serverUrl}/staff/dashboard/get_15_staff_notices/${id}`);
  }
  getEventList(id:number){
    return this.http.get(`${environment.serverUrl}/staff/dashboard/get_15_staff_events/${id}`);
  }
  getHolidays(){
    return this.http.get(`${environment.serverUrl}/staff/dashboard/get_holidays`).pipe(
      map((data)=>{
        if(data instanceof Array){
          data.forEach((holiday)=>{
            holiday.holidayDate=new Date(holiday.holidayDate.toString());
          });
          return data;
        }
      })
    );
  }
  downloadFile(id:number){
    return this.http.get(`${environment.serverUrl}/staff/document/${id}`,{responseType:'arraybuffer'});
  }
  getSpecifiedNotice(){
    return this.http.get(`${environment.serverUrl}/staff/notice/get_specified_notices/${sessionStorage.getItem('username')}`);
  }
  uploadNotice(formdata:FormData){
    return this.http.post(`${environment.serverUrl}/staff/notice/upload`,formdata);
  }
  deleteNotice(id:number){
    return this.http.get(`${environment.serverUrl}/staff/notice/delete/${id}`);
  }
}
