import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Branch } from 'src/app/entity/branch';
import { AdminService } from 'src/app/service/admin.service';

@Component({
  selector: 'app-branch-management',
  templateUrl: './branch-management.component.html',
  styleUrls: ['./branch-management.component.css']
})
export class BranchManagementComponent implements OnInit {

  availability:boolean=false;
  branchList:Branch[];
  constructor(private adminService:AdminService) { }

  ngOnInit(): void {
    this.adminService.getBranchList().subscribe(
      (response:Branch[])=>{
        this.branchList = response;
      }
    );
  }

  addBranch(form:NgForm){
    this.adminService.checkAvailability(form.value.branchId).subscribe(
      (response:boolean)=>{
        if(response == true){
          const formdata:FormData = new FormData();
          formdata.append("branchId",form.value.branchId);
          formdata.append("branchName", form.value.branchName);
          this.adminService.addBranch(formdata).subscribe(
            response=>{
              alert('branch added !');
              document.getElementById('close-branch-add').click();
              this.ngOnInit();
            },
            error=>{
              alert('failed to add !');
            }
          );
        }
        else{
          alert('branch already has been registered');
        }
      }
    );
  }

  deleteBranch(id:number){
    if(confirm('do you want to delete branch with id: '+id+' ?')){
      this.adminService.deleteBranch(id).subscribe(
        response=>{
          alert("deleted !");
          this.ngOnInit();
        },
        error=>{
          alert('failed !');
        }
      );
    }
  }

}
