import { HttpErrorResponse, HttpResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LoginUser } from '../entity/loginuser';
import { Registration } from '../entity/registration';
import { User } from '../entity/user';
import { LoginService } from '../service/login.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  logoutFlag=false;
  public loginUser:LoginUser = {id:"",password:""};
  
  constructor(private loginService:LoginService, private router:Router) { }

  ngOnInit(): void {
    this.loginUser.id="";
    this.loginUser.password="";
    if(sessionStorage.getItem("username")){
      this.loginService.checkAuth().then(
        (user:User)=>{
          this.router.navigate([`/${user.role.toLowerCase()}`]);
        },(error:HttpErrorResponse)=>{});
    }    
  }
  
  

  loginFormSubmit(){
      this.loginService.login(this.loginUser).subscribe((response:User)=>{
      this.router.navigate([`/${response.role.toLowerCase()}`]);
     },
     (error:HttpErrorResponse)=>{
       
       if(error.status===401){
         alert("User Not Found");
       }else if(error.status==500){
         alert("Wrong Password");
       }
       else{
         alert(error.message);
       }
     });
  }

}
