import { HttpErrorResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { DomSanitizer } from '@angular/platform-browser';
import { Branch } from 'src/app/entity/branch';
import { StaffProfile } from 'src/app/entity/staff-profile';
import { AdminService } from 'src/app/service/admin.service';

@Component({
  selector: 'app-staff-profile-management',
  templateUrl: './staff-profile-management.component.html',
  styleUrls: ['./staff-profile-management.component.css']
})
export class StaffProfileManagementComponent implements OnInit {

  userId:string;
  validId:boolean = false;
  branches:Branch[];
  branch:number=0;
  position:string='0';
  allStaffList:StaffProfile[];
  sortedStaffList:StaffProfile[];
  viewProfile:StaffProfile={id:'', address:'', branch:{branchId:null,branchName:''}, email:'', mobile:null, name:'', photo:null, position:''};
  editProfile:StaffProfile;
  image:File;
  constructor(private adminService:AdminService, public sanitizer:DomSanitizer) { }

  
  ngOnInit(): void {
    this.adminService.getBranchList().subscribe(
      (response:Branch[])=>{this.branches = response}
    );
    this.adminService.getAllStaffList().subscribe(
      (response:StaffProfile[])=>{this.allStaffList = response;this.sortedStaffList = response}
    );
  }

  deleteStaffMember(id:string){
    if(confirm('Do you want to delete "'+id+'" ?')){
      this.adminService.deleteStaffMember(id).subscribe(
        (response)=>{
          alert(id+" deleted !");
          this.ngOnInit();
        }
      );
    }
  }

  openModal(profile:StaffProfile, mode:string){
    const container = document.getElementById("staff-mng-container");
    const button = document.createElement('button');
    button.type = 'button';
    button.style.display ='none';
    button.setAttribute('data-toggle','modal');
    if(mode == 'add'){
      this.validId=false;
      this.userId=null;
      this.image = null;
      button.setAttribute('data-target', '#staff-register-modal');
    }
    if(mode == 'edit'){
      this.editProfile = profile;
      this.image = null;
      button.setAttribute('data-target', '#edit-staff-modal');
    }
    if(mode == 'view'){
      this.viewProfile=profile;
      button.setAttribute('data-target','#view-staff-modal');
    }
    container.appendChild(button);
    button.click();
  }

  updateStaffMember(form:NgForm){
    const formdata:FormData = new FormData();
    formdata.append('id', form.value.id);
    if(this.editProfile.position != form.value.position){
      formdata.append('position', form.value.position);
    }
    if(form.value.password != ''){
      formdata.append('password', form.value.password);
    }
    if(this.editProfile.email != form.value.email){
      formdata.append('email', form.value.email);
    }
    if(this.editProfile.mobile != form.value.mobile){
      formdata.append('mobile', form.value.mobile);
    }
    if(this.editProfile.address != form.value.address){
      formdata.append('address', form.value.address);
    }
    if(this.image != null){
      formdata.append('photo', this.image);
    }
    document.getElementById('close-edit-form').click();
    this.adminService.updateStaffMemeber(formdata).subscribe(
      (response)=>{
        alert("update "+form.value.id+" successfully");
        this.ngOnInit();
      }
    );
  }

  sortProfileList(){
    if(this.position == '0' && this.branch == 0){
      this.sortedStaffList = this.allStaffList;
    }
    else if(this.position == '0' && this.branch != 0){
      this.sortedStaffList = this.allStaffList.filter(profile=>profile.branch.branchId == this.branch);
    }
    else if(this.position != '0' && this.branch == 0){
      this.sortedStaffList = this.allStaffList.filter(profile=>profile.position == this.position);
    }
    else{
      this.sortedStaffList = this.allStaffList.filter(profile => profile.branch.branchId == this.branch && profile.position == this.position);
    }
  }

  onImageSelect(mode:string,event){
    this.image=event.target.files[0];
    if(this.image.size > (100*1024)){
      alert("Maximum 100KB alllowed !");
      this.image=null;
      if(mode == 'add')
        document.forms['addStaffMemberForm']['photo'].value='';
      if(mode == 'edit')
        document.forms['staffUpdateForm']['photo'].value='';
    }
  }

  isValidId(checkid:NgForm){
    const id = checkid.value.id;
    if(id == null){
      alert("Id required !");
      return;
    }
    this.adminService.checkIdAvailability(id).subscribe(
      (resp:boolean)=>{
        this.validId=resp;
        if(!resp){
          alert("Given id already taken");
        }
        else{
          this.userId=id;
        }
      }
    );
  }

  addStaffMember(form:NgForm){
    const formdata:FormData = new FormData();
    formdata.append('id',form.value.userId);
    formdata.append('password', form.value.password);
    formdata.append('name', form.value.fname);
    const br:Branch[] = this.branches.filter(b=>form.value.branch==b.branchId);
    formdata.append("branchId",br[0].branchId.toString()); 
    formdata.append('branchName',br[0].branchName);
    formdata.append('position', form.value.position);
    formdata.append('mobile', form.value.mobile);
    formdata.append('email', form.value.email);
    formdata.append('address', form.value.address);
    formdata.append('photo', this.image);
    this.adminService.addStaffMember(formdata).subscribe(
      (response)=>{
        alert("Staff member added !")
        const button = document.getElementById("close-reg-form");
        button.click();
        this.ngOnInit();
      },
      (error:HttpErrorResponse)=>{alert("Failed to add staff member !")}
    );
  }

  AvoidSpace(event) {
    var k = event.which;
    if (k == 32) return false;
  }

}
